.cardbetterPerson {
  position: relative;
  background: white;
  height: 200px;
  width: 200px;
  border-radius: 12px;
  border-width: 1px;
  border-color: white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  margin-right: 8px;
  margin-left: 8px;
  cursor: pointer;
  width: 95%;
  height: 98%;
}

@media (min-width: 1600px) {
  .swiperCardPerson {
    height: 280px;
  }
}

@media (max-width: 1424px) {
  .swiperCardPerson {
    height: 370px;
  }
  .cardbetterPerson button {
    top: 87px;
  }
}

.containerCarousel {
  width: 100%;
}

.titleJobCard {
  border-radius: unset;
  position: relative;
  background: #da2021;
  height: auto;
}

.titleJobCard .titleText {
  font-size: 17px;
  padding-top: 7px;
  color: white;
}

.cardbetterPerson .bodyCardPerson {
  padding-top: 10px;
  margin: 10px;
}

.cardbetterPerson button {
  position: relative;
  background: #fefefe;
  color: #da2021;
  border-radius: 12px;
  border-color: transparent;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  margin-bottom: 2px;
}

.cardbetterPerson button:hover {
  background: #da2021;
  color: #fefefe;
  transition: all 0.1s ease;
}

.bodyCardPerson .col img {
  position: relative;
  bottom: 15%;
  height: 140px;
  width: 140px;
  border-radius: 100%;
  margin-bottom: 5px;
}

.bodyCardPerson .col b {
  font-size: 13px;
  justify-content: left;
  font-weight: bolder;
}

.bodyCardPerson .cardContent {
  margin-top: 8px;
}

.bodyCardPerson .cardContent .firstContent {
  height: 170px;
}

.titleUser {
  color: #da2021;
}

.bodyCardPerson .col li {
  justify-content: left;
  list-style-type: none;
  text-align: left;
}

.bodyCardPerson .userinfo {
  position: relative;
  bottom: 18%;
}

.bodyCardPerson .ratingUser .bxs-star.true {
  color: rgb(255, 221, 31);
  position: relative;
  bottom: 20%;
}

.bodyCardPerson .ratingUser {
  position: relative;
  bottom: 30%;
  margin-top: 0;
}

.calitifationstring {
  font-size: 13px;
  color: #da2021;
  margin-top: 2px;
}
